import React, { Component } from "react";
import TypingEngine from "../../typing/TypingEngine";
import TouchTypeHelper from "../../typing/TouchTypeHelper";
import WPMDisplay from "../../typing/WPMDisplay";
import "./Lesson.scss";
import { connect } from "react-redux";
import lessons from "../../../assets/lessons";

class Lesson extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { title, author, text } = this.props.lesson;
    const { currentChar, wpm } = this.props;

    return (
      <div className="container">
        {
          <div
            style={{ marginBottom: "1em", padding: "8px", textAlign: "center" }}
          >
            <span className="title">{title ? title : "\xa0"}</span>
            <br />
            <span className="author">{author ? `${author}` : "\xa0"}</span>
          </div>
        }

        <div className="row">
          <div className="col-md-7">
            {text ? <TypingEngine text={text} /> : <div />}
          </div>
          <div className="col-md-5">
            <TouchTypeHelper character={currentChar} />
          </div>
        </div>
      </div>
    );
  }
}

const WORD_SIZE = 5;
function getMinutesBetweenDates(startDate, endDate) {
  var diff = endDate.getTime() - startDate.getTime();
  return diff / 60000;
}

const mapStateToProps = (state, ownProps) => {
  const { section, currentCharIndex, startTime } = state.typingEngine;
  const currentChar = section ? section[currentCharIndex] : undefined;

  let wpm;
  if (startTime) {
    const minutes = getMinutesBetweenDates(startTime, new Date());
    wpm =
      minutes === 0
        ? undefined
        : Math.round(currentCharIndex / WORD_SIZE / minutes);
  }

  return {
    lesson: lessons[ownProps.params.id],
    currentChar,
    wpm,
  };
};

export { Lesson };
export default connect(mapStateToProps)(Lesson);
