export const KEYPRESS = 'TYPING_ENGINE:KEYPRESS'
export const BACKSPACE = 'TYPING_ENGINE:BACKSPACE'
export const SET_SECTIONS = 'TYPING_ENGINE:SET_SECTIONS'
export const SET_CURRENT_SECTION = 'TYPING_ENGINE:SET_CURRENT_SECTION'

export const keypress = (key) => {
  return (dispatch, getState) => {
    const state = getState().typingEngine
    const nextCharIndex = state.currentCharIndex + 1
    if (nextCharIndex === state.section.length) {
      return dispatch(setCurrentSection(state.sectionIndex + 1))
    }

    return dispatch({ type: KEYPRESS, payload: key })
  }
}

export const backspace = () => ({ type: BACKSPACE })
export const setSections = (sections) => ({ type: SET_SECTIONS, payload: sections })
export const setCurrentSection = (sectionIndex) => ({ type: SET_CURRENT_SECTION, payload: sectionIndex })

export const actions = {
  keypress,
  backspace,
  setSections,
  setCurrentSection
}

function transformChar(c) {
  switch (c) {
    case '‘': { // emdash
      return `'` // regular dash
    }
    case '’': { // emdash
      return `'` // regular dash
    }
    case '–': { // emdash
      return '-' // regular dash
    }
    case '—': {
      return '-' // regular dash
    }
    default: {
      return c
    }
  }
}

const ACTION_HANDLERS = {
  [SET_SECTIONS]: (state, action) => { 
    return {
      ...state,
      sections: action.payload,
      //section: action.payload[0],
      numSections: action.payload.length
    }
  },
  [SET_CURRENT_SECTION]: (state, action) => { 
    return {
      ...state,
      section: state.sections[action.payload],
      sectionIndex: action.payload,
      currentCharIndex: 0
    }
  },
  [KEYPRESS]: (state, action) => { 
    const currentChar = state.section[state.currentCharIndex]
    const transformedChar = transformChar(currentChar)

    if ((state.backspacesRequired > 0) || (action.payload !== transformedChar)) {
      return { 
        ...state,
        backspacesRequired: state.backspacesRequired + 1,
        startTime: state.startTime || new Date()
      }
    }

    return {
      ...state,
      currentCharIndex: state.backspacesRequired === 0 ? state.currentCharIndex + 1 : state.currentCharIndex,
      startTime: state.startTime || new Date()
    }
  },
  [BACKSPACE]: (state, action) => {
    const newBackspacesRequired = state.backspacesRequired <= 0 ?
      0 : state.backspacesRequired - 1

    let newIndex;
    if (state.backspacesRequired === 0) {
      newIndex = state.currentCharIndex > 0 ? state.currentCharIndex - 1 : 0
    } else {
      newIndex = state.currentCharIndex
    }

    return {
      ...state,
      currentCharIndex: newIndex,
      backspacesRequired: newBackspacesRequired
    }
  }
}

// ------------------------------------
// Reducer
// ------------------------------------
const initialState = { 
  sections: [],
  section: undefined,
  numSections: undefined,
  sectionIndex: 0,
  currentCharIndex: 0, 
  backspacesRequired: 0
}

export default function typingEngineReducer (state = initialState, action) {
  const handler = ACTION_HANDLERS[action.type]

  return handler ? handler(state, action) : state
}
