import classNames from "classnames";
import "./TypingEngine.scss";
import React, { Component } from "react";
import { throttle } from "throttle-debounce";
import _ from "lodash";
import $ from "jquery";
import { connect } from "react-redux";
import {
  setSections,
  setCurrentSection,
  keypress,
  backspace,
} from "./TypingEngineModule";

const BACKSPACE_KEYCODE = 8;
const ENTER_KEYCODE = 13;
const SPACE_KEYCODE = 32;

class TypingEngine extends Component {
  constructor(props) {
    super(props);
    this.handleKeypress = this.handleKeypress.bind(this);
    this.handleKeydown = this.handleKeydown.bind(this);
    this.handleNextClick = this.handleNextClick.bind(this);
    this.handlePreviousClick = this.handlePreviousClick.bind(this);
    this.handleBackspace = throttle(10, this.handleBackspace.bind(this));
  }

  handleBackspace() {
    this.props.onBackspace();
  }

  handleNextClick() {
    this.props.setCurrentSection(this.props.sectionIndex + 1);
    this._nextButton.blur();
  }

  handlePreviousClick() {
    this.props.setCurrentSection(this.props.sectionIndex - 1);
    this._previousButton.blur();
  }

  componentDidMount() {
    window.addEventListener("keypress", this.handleKeypress);
    window.addEventListener("keydown", this.handleKeydown);
  }

  componentWillUnmount() {
    window.removeEventListener("keypress", this.handleKeypress);
    window.removeEventListener("keydown", this.handleKeydown);
    this.props.setSections([]);
  }

  handleKeypress(ev) {
    const c = String.fromCharCode(ev.which);
    if (c === `'`) {
      ev.preventDefault();
    }
    this.props.onKeypress(c);
  }

  handleKeydown(ev) {
    if (ev.keyCode === BACKSPACE_KEYCODE) {
      ev.preventDefault();
      this.handleBackspace();
    }

    if (ev.keyCode === ENTER_KEYCODE) {
      ev.preventDefault();
      this.props.onKeypress("\n");
    }

    if (ev.keyCode === SPACE_KEYCODE) {
      ev.preventDefault();
      this.props.onKeypress(" ");
    }
  }

  handleFirstKeyPress() {
    // start WPM timer
  }

  handleLastKeyPress() {
    // stop WPM timer, push WPM onto array
  }

  render() {
    const {
      text,
      section,
      sectionIndex,
      sections,
      setSections,
      setCurrentSection,
      currentCharIndex,
      backspacesRequired,
    } = this.props;

    const disablePrevButton = sections && sectionIndex === 0;
    const disableNextButton = sections && sectionIndex >= sections.length - 1;

    return (
      <div className={classNames("pull-right engineContainer")}>
        {sections.length ? (
          <SectionView
            section={section}
            position={currentCharIndex}
            backspacesRequired={backspacesRequired}
          />
        ) : (
          <SectionsCalculator
            text={text}
            onDone={(sections) => {
              setSections(sections);
              setCurrentSection(0);
            }}
          />
        )}

        <div className={classNames("container-fluid", "sectionTextBottom")}>
          <div className="row">
            <div className={classNames("col-xs-4 text-left", "noPadding")}>
              <button
                onClick={
                  disablePrevButton ? () => {} : this.handlePreviousClick
                }
                ref={(b) => {
                  this._previousButton = b;
                }}
                type="button"
                className={classNames("btn btn-default btn-xs", {
                  disabled: disablePrevButton,
                })}
              >
                <span
                  className={classNames(
                    "glyphicon glyphicon-menu-left",
                    "grey"
                  )}
                ></span>
              </button>
            </div>

            <div className={classNames("col-xs-4 text-center")}>
              <span className="pageNum">
                {sections ? `${sectionIndex + 1} / ${sections.length}` : "-"}
              </span>
            </div>

            <div className={classNames("col-xs-4 text-right", "noPadding")}>
              <button
                onClick={disableNextButton ? () => {} : this.handleNextClick}
                ref={(b) => {
                  this._nextButton = b;
                }}
                type="button"
                className={classNames("btn btn-default btn-xs", {
                  disabled: disableNextButton,
                })}
              >
                <span
                  className={classNames(
                    "glyphicon glyphicon-menu-right",
                    "grey"
                  )}
                ></span>
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const SectionView = (props) => {
  const { section, position, backspacesRequired } = props;

  const textBeforeCurrentChar = section.slice(0, position);

  let numberOfHighlightedChars = 1;
  if (backspacesRequired > 1) {
    numberOfHighlightedChars += backspacesRequired - 1;
  }

  const currentChar = section.slice(
    position,
    position + numberOfHighlightedChars
  );
  const textAfterCurrentChar = section.slice(
    position + numberOfHighlightedChars
  );
  const currentCharClassNames = classNames("current", {
    correct: backspacesRequired <= 0,
    incorrect: backspacesRequired > 0,
  });

  return (
    <div id="sectionText" className="sectionText">
      <p>
        {textBeforeCurrentChar}
        <span className={currentCharClassNames}>
          {currentChar === "\n" ? (
            <span className={classNames(currentCharClassNames, "enter")}>
              ⏎
            </span>
          ) : null}
          {currentChar}
          {backspacesRequired > 0 ? <span className="backspace">⌫</span> : null}
        </span>
        {currentChar === " " ? (
          <span className={classNames(currentCharClassNames, "space")}> </span>
        ) : null}
        {textAfterCurrentChar}
      </p>
    </div>
  );
};

class SectionsCalculator extends Component {
  constructor(props) {
    super(props);
    this.calcSection = this.calcSection.bind(this);
    this.optimizingConstant = 1000;
    this.loaded = false;
  }

  componentWillMount() {
    console.time("calc");
    this.setState(
      {
        currentSectionStart: 0,
        currentSectionEnd: this.optimizingConstant,
        sections: [],
        loaded: false,
      },
      this.calcSection
    );
  }

  render() {
    const { text } = this.props;
    const { currentSectionStart, currentSectionEnd, loaded } = this.state;

    return (
      <SectionView
        section={text.slice(currentSectionStart, currentSectionEnd)}
        position={0}
      />
    );
  }

  calcSection() {
    const { text, onDone } = this.props;
    const { currentSectionStart, currentSectionEnd, sections } = this.state;

    const sectionViewEl = $("#sectionText");

    const hasOverflowed =
      sectionViewEl[0].scrollHeight > sectionViewEl.innerHeight() - 1;

    if (currentSectionEnd >= text.length) {
      const res = _.map(sections, (section) => {
        return text.slice(section[0], section[1]);
      });
      res.push(text.slice(currentSectionStart, currentSectionEnd)); // hack
      console.timeEnd("calc");
      this.setState({ loaded: true });
      return onDone(res);
    }

    if (hasOverflowed) {
      const lastWhiteSpaceIndex = _.findLastIndex(
        text.slice(0, currentSectionEnd - 1),
        (c) => {
          return /\s/.test(c);
        }
      );
      const endIndex = lastWhiteSpaceIndex + 1;
      const nextSectionStartingIndex = endIndex;
      const nextSectionEndIndex =
        nextSectionStartingIndex + this.optimizingConstant >= text.length
          ? text.length
          : nextSectionStartingIndex + 0;

      this.setState((prevState, currentProps) => {
        prevState.sections.push([currentSectionStart, endIndex]);

        return {
          sections: prevState.sections,
          currentSectionStart: nextSectionStartingIndex,
          currentSectionEnd: nextSectionEndIndex + this.optimizingConstant,
        };
      }, this.calcSection);
    } else {
      let nextWhiteSpaceDistance = _.findIndex(
        text.slice(currentSectionEnd),
        (c) => {
          return /\s/.test(c);
        }
      );
      if (nextWhiteSpaceDistance === -1) {
        nextWhiteSpaceDistance = 0;
      }
      const nextSectionEndIndex =
        currentSectionEnd + nextWhiteSpaceDistance + 1;

      this.setState(
        (prevState, currentProps) => ({
          currentSectionEnd: nextSectionEndIndex,
        }),
        this.calcSection
      );
    }
  }
}

// Container

const mapActionCreators = {
  onKeypress: keypress,
  onBackspace: backspace,
  setSections,
  setCurrentSection,
};

const mapStateToProps = (state) => ({
  section: state.typingEngine.section,
  sections: state.typingEngine.sections,
  numSections: state.typingEngine.numSections,
  sectionIndex: state.typingEngine.sectionIndex,
  currentCharIndex: state.typingEngine.currentCharIndex,
  backspacesRequired: state.typingEngine.backspacesRequired,
});

export default connect(mapStateToProps, mapActionCreators)(TypingEngine);
export { TypingEngine };
